@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
*, *::before, *::after {
  box-sizing: border-box;
}
html,body{
  padding: 0;
  margin: 0;
  /* background: #000; */
  /* color: #fff; */
}

#root {
  width: 640px;
  margin: 0 auto;
}

button {
  background: #3085e8;
  padding: 6px 16px;
  border-radius: 2px;
  color: #fff;
  font-size: 14px;
}

.sort-btn {
  margin-left: 40px;
  margin-bottom: 20px;
}

.options {
  margin-top: 100px;
}

.sort-nums {
  max-height: 100px;
  overflow: scroll;
}

.cards {
  position: relative;
  height: 400px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  /* background-color: #000; */
}

.card-wrapper {
  position: absolute;
  bottom: 0;
  border: 1px solid transparent;
  transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -webkit-transition: -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 200ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.card {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #ff3179;
}

.card-active {
  -webkit-filter: hue-rotate(200deg);
          filter: hue-rotate(200deg);
}

.card-locked {
  -webkit-filter: hue-rotate(280deg);
          filter: hue-rotate(280deg);
}

.value {
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 1.25rem;
}

.control-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 30px 5px 0;
  padding-top: 20px;
  border-top: 1px solid #fff;
}

h1 {
  margin: 0;
  font-size: 2.5rem;
}

